#logo {
  font-size: 2em;
  font-weight: 300;
  font-family: "Futura";
}

@font-face {
  font-family: "Hanken Grotesk";
  src: url(/static/media/HankenGrotesk-Regular.8aa432c0.eot);
  src:url(/static/media/HankenGrotesk-Regular.a3d1b1bb.woff)format("woff"),
  url(/static/media/HankenGrotesk-Regular.9e8bb12f.otf)format("opentype")
}

@font-face {
  font-family: "Hanken Grotesk Black";
  src: url(/static/media/HankenGrotesk-Black.499224d6.eot);
  src:url(/static/media/HankenGrotesk-Black.ea8cbdeb.woff)format("woff"),
  url(/static/media/HankenGrotesk-Black.616709b8.otf)format("opentype")
}

@font-face {
  font-family: "Hanken Grotesk Medium";
  src: url(/static/media/HankenGrotesk-Medium.11157450.eot);
  src:url(/static/media/HankenGrotesk-Medium.7ed163a6.woff)format("woff"),
  url(/static/media/HankenGrotesk-Medium.15c02b41.otf)format("opentype")
}

#about-text p {
  font-family: "Hanken Grotesk";
  font-size: 8vw;
  font-weight: bold;
  color: white;
  text-align: center;
}

#about-text {
  padding-top: 15%;
}

#about-row {
  height: 90vh;
  background-color: black;
}

#small-descriptor {
  border-right: solid 1px white;
  max-height: 10vh;
}

#small-descriptor p, #second-small-descriptor  p {
  font-size: 1vw;
  color: white;
}

@media screen and (max-width: 600px) {
  #about-row {
    height: 35vh;
  }

  #about-text {
    padding-top: 5%;
  }

  #about-button {
    font-size: 2.5vw;
  }

  #small-descriptor {
    height: 15vh;
  }

  #small-descriptor p, #second-small-descriptor  p {
    font-size: 2vw;
    color: white;
  }
}

#landing-row p {
  color: white;
  font-family: "Hanken Grotesk Black";
  font-size: 4vw;
  padding-top: 5%;
  padding-left: 5%;
  padding-bottom: 25%;
}

#landing-row a {
  font-family: "Hanken Grotesk";
  font-size: 4vw;
  padding-left: 5%;
  padding-bottom: 5%;
  color: white;
}

#landing-row a:hover {
  text-decoration: underline;
}

#landing-row {
  max-height: 95vh;
  background-color: #5D486D;
}

#categories {
  background-color: #5D486D;
}

#side-img {
  background-color: white;
}

#side-img img {
  padding: 5vh;
  height: 90vh;
  width: 75vh;
}

@media screen and (max-width: 600px) {
  #landing-row p {
    padding-bottom: 0;
  }

  #side-img img {
    padding: 0;
    height: 35vh;
    width: 30vh;
    overflow: hidden;
  }
}

#brief-row {
  max-height: 40vh;
  background-color: #EBE8ED;
}

#text-description {
  color: black;
  text-align: center;
  padding-top: 5%;
  padding-bottom: 5%;
}

@media screen and (max-width: 600px) {
  #text-description {
    padding-bottom: 0;
  }
}

#banner-row {
  height: 90vh;
}

#ladder-text {
  padding-top: 5%;
}

#ladder-img {
  padding-top: 5%;
}

@media screen and (max-width: 600px) {
  #banner-row {
    height: 30vh;
  }

  #ladder-img {
    padding-top: 5%;
    height: 25vh;
  }
}

.athena-small-widget {
  height: 40vh;
  background: url(/static/media/rit.99ccfd66.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-color: black;
}

#card-body {
  margin-top: 25vh;
  height: 5vh;
  color: white;
  background: rgba(154, 141, 164, 0.8);
}

.athena-small-widget p {
  font-size: 1vw;
}

.athena-small-widget h3 {
  font-size: 1.5vw;
  height: 3vw;
}

html, body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  border-radius: 0 !important;
}

header {
  font-size: 1.2rem;
}

h1 {
  font-family: "Hanken Grotesk Black";
  font-size: 7vw;
}

h2 {
  font-family: "Hanken Grotesk Black";
  font-size: 3vw;
}

h3 {
  font-family: "Hanken Grotesk Black";
  font-size: 2.5vw;
}

p {
  font-family: "Hanken Grotesk";
  font-size: 2vw;
}

.athena-primary {
  font-family: "Hanken Grotesk Medium";
  background-color: black;
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 1.5vw;
  padding-left: 10%;
  padding-right: 10%;
}

.athena-primary-white {
  font-family: "Hanken Grotesk Medium";
  background-color: white;
  border: none;
  color: black;
  text-align: center;
  text-decoration: none;
  font-size: 1.5vw;
  padding-left: 10%;
  padding-right: 10%;
}

